				fatherLastName: '',
				motherLastName: '',
				rut: '',
				date: '',
				lang: {
			        formatLocale: {
			          firstDayOfWeek: 0,
			          weekdaysMin: ['Dom', 'Lun', 'Mar', 'Mie', 'Jue', 'Vie', 'Sab'],
			        },

			        months: ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'],
			    },

				phone: '+56',
				email: '',

			    //Vue Dropzone data
			    tempAttachments: [],
			    attachments: [],
			    dropzoneOptionsPN: {
			        // The Url Where Dropped or Selected files will be sent
			        url: `https://httpbin.org/post`,
			        // File Size allowed in MB
			        maxFilesize: 10,
			        // Authentication Headers like Access_Token of your application
			        headers: {
			          Authorization: `Access Token`,
			        },
			        // The way you want to receive the files in the server
			        paramName: function (n) {
			          return "file[]";
			        },
			        //dictDefaultMessage: "Upload Files Here xD",
			        //includeStyling: false,
			        //previewsContainer: false,
			        thumbnailWidth: 250,
			        thumbnailHeight: 140,
			        uploadMultiple: true,
			        parallelUploads: 20,
			        addRemoveLinks: true,
			        dictRemoveFile: 'Eliminar archivo',
			        dictCancelUpload: 'Cancelar subida',
			        dictInvalidFileType: 'No puede subir archivos con este formato.',
			        dictFileTooBig: "El archivo es muy grande ({{filesize}}MB). Mximo: {{maxFilesize}}MB.",
			        acceptedFiles: '.jpg, .jpeg, .xls, .xlsx, .pdf, .doc, .docx',
			    },

			    street: '',
			    streetNumber: '',
				office: '',
				reference: '',

				dataComites: {
			        contratista: false,
			        obras: false,
			        concesiones: false,
			        inmobiliario: false,
			        vivienda: false,
			        proveedores: false,
			        industriales: false,
			        especialidades: false
			    },

				nameIsValid: '',
				firstLastnameIsValid: '',
				secondLastNameIsValid: '',
				dateIsValid: '',
				telIsValid: '',
				emailIsValid: '',

				professionIsValid: '',
				specialtyIsValid: '',
				dropzoneIsValid: '',
				regionIsValid: '',
				provinceIsValid: '',
				communeIsValid: '',
				streetIsValid: '',
				streetNumberIsValid: '',
				referenceIsValid: '',
				officeIsValid: '',
				formIsValid: '',
				requestNumberValue: '',
				datosBasicos: '',
				getfirstStep: '',
				firstStepObject: [],
			}
		},

		methods: {
			...mapMutations(['focus', 'blur', 'dateBlur', 'rutValidation', 
